<template>
    <div>
        <input class="todoTitleEntry" type="text" placeholder="Todo Title">
        <button v-on:click="makeTodo">Add</button>
    </div>
</template>

<script>
export default {
    name: 'createTodo',
    props: ['todos'],
    methods: {
        makeTodo(){
            const key = 'todos'
            const finalstorage = JSON.parse(localStorage.getItem(key))

            const titleInput = document.querySelector('.todoTitleEntry');
            if (titleInput.value){
                const obj = {
                    id: this.todos.length+1,
                    title: titleInput.value,
                    completed: false,
                }
                console.log(this.todos.length)
                this.todos.push(obj);
                finalstorage.push(obj);
                titleInput.textContent = '';
                localStorage.setItem(key, JSON.stringify(finalstorage))
            }
        }
    }
}
</script>