<template>
    <div class="todo-item" v-bind:class="{'is-completed':todo.completed}">
        <p>
            <input class="checkbox" type="checkbox" v-on:change="markcomplete">
            {{todo.title}}
            <button class="remove" v-on:click="removeMe">X</button>
        </p>
    </div>
</template>

<script>
export default {
    name: 'todoItem',
    props: ['todo', 'todos'],
    methods: {
        markcomplete(){
            const key = 'todos'
            const finalstorage = JSON.parse(localStorage.getItem(key))
            const i = this.todos.indexOf(this.todo)
            this.todo.completed = !this.todo.completed;
            finalstorage[i].completed = this.todo.completed;
            localStorage.setItem(key, JSON.stringify(finalstorage))
        },
        removeMe(){
            const key = 'todos'
            const finalstorage = JSON.parse(localStorage.getItem(key))
            const i = this.todos.indexOf(this.todo)
            if (i === 0){
                this.todos.shift()
                finalstorage.shift()
            }else{
                this.todos.splice(i,i)
                finalstorage.splice(i,i)
            }
            localStorage.setItem(key, JSON.stringify(finalstorage))
        }
    }
}
</script>

<style scoped>
    .todo-item{
        background-color: aquamarine;
        padding: 1vh 0 1vh 0;
        text-align: left;
        border-radius: 15px;
        margin-top: 1vh;
    }
    .is-completed{
        text-decoration: line-through;
    }
</style>