<template>
  <div id="app">
    <Create v-bind:todos="todos"/>
    <Todos v-bind:todos="todos"/>
  </div>
</template>

<script>
import Todos from './components/Todos.vue'
import Create from './components/createTodo.vue'

const key = 'todos'
const storage = localStorage.getItem(key)

if (!storage){
  localStorage.setItem(key, JSON.stringify([]))
}

const finalStorage = JSON.parse(localStorage.getItem(key))

export default {
  name: 'App',
  components: {
    Todos,
    Create
  },
  data(){
    return {
      todos: finalStorage
    }
  }
}
</script>

